.App{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Avenir';
}

:root {
    --color1:#00ffb3;
    --color2:#4887fc;
}

/*reset appearance of input boxes and buttons*/
.login input, .login button, .App button {
    appearance: none;
    background: none;
    border: none;
    outline: none;
}

/*sets page size*/
.App {
    display: block;
    align-items: flex-start;
    justify-content: center;
    background-color: #ffffff
}

.login form {
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.login form:after {
    content: '';
    display: block;
    position: absolute;
    top: -5px;
    left: -5px;
    right: -5px;
    bottom: -5px;
    z-index: 1;
    background-image: linear-gradient(to bottom right, var(--color1), var(--color2));
}

.login form .form-inner{
    position: relative;
    display: block;
    background-color: #FFF;
    padding: 30px;
    z-index: 2;
}

.login form .form-inner h2{
    color: #888;
    font-size: 2em;
    font-weight: 500;
    margin-bottom: 15px;
}

.login form .form-inner .form-group{
    display: block;
    width: 300px;
    margin-bottom: 15px;
}

.login .form-inner .form-group label{
    display: block;
    color: #666;
    font-size: 1em;
    margin-bottom: 5px;
    transition: 0.4s;
}

.login .form-inner .form-group:focus-within label {
    color: var(--color2);
}

.login form .form-inner .form-group input {
    display: block;
    width: 100%;
    padding: 10px 15px;
    background-color: #F8F8F8;
    border-radius: 8px;
    transition: 0.4s;
}

.login form .form-inner .form-group input:focus {
    box-shadow: 0px 0px 3px rgba(0,0,0,0.2);
}

/*Button appearance and animation*/
.login form .form-inner input[type="submit"], .welcome button{
    display: inline-block;
    padding: 10px 15px;
    border-radius: 8px;
    background-image: linear-gradient(to right, var(--color1) 50%, var(--color1) 50%, var(--color2));
    background-size: 200%;
    background-position: 0%;
    transition: 0.4s;
    color: #FFF;
    font-weight: 700;
    cursor: pointer;
}

.login form .form-inner input[type="submit"]:hover, .welcome button:hover{
    background-position: 100% 0%;
}

 .welcome {
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(255, 255, 255);
}

 .welcome h2 {
    color: #888;
    font-size: 10vh;
    font-weight: 500;
    margin-right: 2vh;
}
.welcome button{
    display: inline-flex;
    font-size: 6vh;
}

 .welcome h2 span {
    color: var(--color2);
    font-weight: 700;
}

.submitButtons input{
    margin: 5px;
}